import Vue from 'vue'
import App from './App'
import router from './router'


console.log("sss")
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})


router.beforeEach((to, from, next) => {
  document.title = to.name
  next()
})




